<template>
	<div class="saleedi-find">
		<div class="ent-list">
			<div class="ent-block" v-for="(item, index) in companyList" :key="index">
				<div class="ent-name">{{item.name}}</div>
				<div class="ent-btn" @click="createOrder(item)">前往订货<van-icon name="arrow" class="btn-arrow" /></div>
			</div>
			<div class="add-btn-block" v-show="companyList.length === 0">
				<van-button class="add-btn" type="primary" round @click="addEntClick">+ 添加订货企业</van-button>
			</div>
		</div>
		<div class="order-manage fx-edi-block">
			<div class="block-title">订单管理</div>
			<div class="order-manage-module">
				<orderManage :orderManageData="manageModule" @manageModuleClick="billClickHandler"></orderManage>
			</div>
		</div>
	</div>
</template>

<script>
import orderManage from '@/components/OrderManage.vue'
export default {
	name: 'find',
	components: {
		orderManage
	},
	data () {
		return {
			manageModule: {
				toBeConfirmed: {
					label: '待确认',
					iconSrc: require('@/assets/images/icon/icon-beforeConfirm.png'),
					waring: '0',
					billState: '1',
					showFlag: true
				},
				toBeReceived: {
					label: '待收货',
					iconSrc: require('@/assets/images/icon/icon-beforeReceiving.png'),
					waring: '0',
					billState: '2',
					showFlag: true
				},
				toBeFinished: {
					label: '已完成',
					iconSrc: require('@/assets/images/icon/icon-confirm.png'),
					waring: '0',
					billState: '3',
					showFlag: true
				},
				notUse: {
					label: '点我',
					iconSrc: require('@/assets/images/icon/icon-confirm.png'),
					waring: '0',
					billState: '',
					showFlag: true
				}
			},
			companyList: []
		}
	},
	created () {
		this.getHomeData()
	},
	methods: {
		getHomeData () {
			Promise.all([this.getCompany(), this.getToDOWork()]).then(([companyList, toWorkList]) => {
				this.companyList = companyList
				this.setToWork(toWorkList)
			})
		},
		getCompany () {
			return new Promise(resolve => {
				this.$fxHttp.requestPost({
					url: `${this.$fxBaseApi.url}/getOrderCompany`
				}).then(res => {
					resolve(res)
				})
			})
		},
		getToDOWork () {
			return new Promise(resolve => {
				this.$fxHttp.requestPost({
					url: `${this.$fxBaseApi.url}/getToDOWork`
				}).then(res => {
					resolve(res)
				})
			})
		},
		setToWork (toWorkList) {
			for (let key in toWorkList) {
				this.manageModule[key].waring = toWorkList[key]
			}
		},
		createOrder (item) {
			this.$router.push({
				path: '/createOrder',
				query: {
					companyName: item.name,
					companyId: item.id
				}
			})
		},
		billClickHandler (item) {
			if (item.billState === '') {
				this.$fxHttp.requestPost({
					url: `${this.$fxBaseApi.url}/9001 `
				}).then(res => {
					console.log(res)
				})
			} else {
				const billState = item.billState
				this.$router.push({
					path: '/orderList',
					query: {
						billState
					}
				})
			}
		},
		addEntClick () {
			this.$router.push({
				path: '/bindEnt',
				query: {
					notFirst: true
				}
			})
		}
	}
}
</script>
