<template>
	<div class="saleedi-main">
		<!-- 内容 -->
		<router-view class="saleedi-content" keep-alive></router-view>
		<!-- 底部标签 -->
		<van-tabbar v-model="tabActive" class="saleedi-tabbar" @change="tabbarChange">
			<van-tabbar-item icon="search">发现</van-tabbar-item>
			<van-tabbar-item icon="orders-o">订单</van-tabbar-item>
			<van-tabbar-item icon="friends-o">我的</van-tabbar-item>
		</van-tabbar>
	</div>
</template>

<script>

export default {
	name: 'home',
	data () {
		return {
			tabActive: 0,
			routeList: [
				{ path: 'find' },
				{ path: 'order' },
				{ path: 'me' }
			]
		}
	},
	created () {
		this.tabActive = this.$route.meta && this.$route.meta.tabIndex
	},
	mounted () {
		if (window.history && window.history.pushState) {
			history.pushState(null, null, document.URL)
			window.addEventListener('popstate', this.goBack, false)
		}
	},
	destroyed () {
		window.removeEventListener('popstate', this.goBack, false)
	},
	watch: {
		$route (route) {
			this.tabActive = route.meta && route.meta.tabIndex
		}
	},
	methods: {
		tabbarChange (index) {
			this.$router.push(this.routeList[index])
		},
		goBack () {
			this.$router.replace('/home/find')
		}
	}
}
</script>
<style lang="stylus" scoped>
.saleedi-main {
	height:100%
	background:#fff
	.saleedi-content {
		position: absolute;
		width: 100%;
		top: 0;
		bottom: 50px;
	}
}
</style>
