<template>
	<div class="saleedi-order">
		<div class="saleedi-order-title">
			待确认订单
			<div class="edit-btn fl-r" @click="editBtnClick">{{editBtnText}}</div>
		</div>
		<div class="order-wrap">
			<div class="order-block fx-edi-block" v-for="(item, index) in orderData" :key="index">
				<div class="order-ent-cell">
					<van-checkbox
						v-model="item.checked"
						class="order-checkbox"
						v-show="orderEditFlag"
						@change="entCheckboxChange(arguments, index)"></van-checkbox>
					<div class="order-ent">{{item.organName}}</div>
				</div>
				<div class="order-list">
					<div class="order-list-item" v-for="(listItem, listIndex) in item.bills" :key="listIndex" @click="orderItemClick(listItem.id)">
						<stateTag :billState="listItem.billState" class="order-item-state"></stateTag>
						<div class="order-item-title">
							<div class="order-item-number">订单编号：{{listItem.billNo}}</div>
						</div>
						<div class="order-item-details">
							<van-checkbox v-model="listItem.checked" class="order-details-checkbox" v-show="orderEditFlag"></van-checkbox>
							<div class="order-item-row">
								<div class="order-item-count order-item-cell">明细数量：{{listItem.billDtCount}}</div>
								<div class="order-item-sumCount order-item-cell">数量合计：{{listItem.goodsCount}}</div>
							</div>
							<div class="order-item-sumPrice order-item-cell">金额合计：{{listItem.sumMoneyWithTax}}</div>
							<div class="order-item-date order-item-cell">下单时间：{{listItem.makeDate}}</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="order-bottom" v-show="orderEditFlag">
			<van-checkbox v-model="selectAll" class="select-all-checkbox">全选</van-checkbox>
			<div class="order-bottom-text">已选{{selectCountCoumpted}}条订单</div>
			<van-button type="danger" square >删除订单</van-button>
		</div>
	</div>
</template>

<script>
import stateTag from '@/components/StateTag.vue'
export default {
	name: 'order',
	components: {
		stateTag
	},
	data () {
		return {
			orderData: [],
			orderEditFlag: false,
			selectAll: false
		}
	},
	created () {
		this.orderListInit()
	},
	watch: {
		selectAll (val) {
			this.orderData.forEach(item => {
				item.checked = val
				item.bills.forEach(billsItem => {
					billsItem.checked = val
				})
			})
		}
	},
	computed: {
		editBtnText () {
			return this.orderEditFlag ? '完成' : '编辑'
		},
		selectCountCoumpted () {
			let orderSelectArr = []
			this.orderData.forEach(item => {
				orderSelectArr = orderSelectArr.concat(item.bills)
			})
			let selectCountArr = orderSelectArr.filter(item => {
				return item.checked === true
			})
			return selectCountArr.length
		}
	},
	methods: {
		orderListInit () {
			this.getQueryAllBill().then(res => {
				res.forEach(item => {
					item.checked = false
					item.bills.forEach(billsItem => {
						billsItem.checked = false
					})
				})
				this.orderData = res
			})
		},
		getQueryAllBill () {
			return new Promise(resolve => {
				const params = {
					billState: ''
				}
				this.$fxHttp.requestPost({
					url: `${this.$fxBaseApi.url}/queryAllBill`,
					data: params
				}).then(res => {
					resolve(res)
				})
			})
		},
		editBtnClick () {
			this.orderEditFlag = !this.orderEditFlag
		},
		entCheckboxChange (arg, index) {
			let checked = arg[0]
			this.orderData[index].bills.forEach(item => {
				item.checked = checked
			})
		},
		orderItemClick (id) {
			if (this.orderEditFlag) {
				return false
			}
			this.$router.push({
				path: '/orderDetails',
				query: {
					id: id
				}
			})
		}
	}
}
</script>
