<template>
	<div class="saleedi-me">
		<div class="me-top">
			<div class='me-log'></div>
			<div class="me-user">
				<div class="me-avatar">
					<img class="avatar-img" src="@/assets/images/img/shanglong-logo.jpg">
				</div>
				<div class="me-name">小狮子</div>
				<van-icon name="arrow" class="me-arrow" />
			</div>
		</div>
		<div class="order-manage-box">
			<div class="me-order-manage">
				<div class="order-mange-title">
					<div class="title-name">订单管理</div>
					<div class="see-all-order fl-r" @click="seeAllOrderHandle">查看全部订单<van-icon name="arrow" class="me-arrow" /></div>
				</div>
				<div class="order-manage-module">
					<orderManage :orderManageData="manageModule" @manageModuleClick="billClickHandler"></orderManage>
				</div>
			</div>
		</div>
		<div class="me-menu-list">
			<van-cell title="报表查询" is-link to="/reportForm">
				<i slot="icon" class="report-form-icon"></i>
			</van-cell>
			<van-cell title="收货地址" is-link to="/addressList">
				<i slot="icon" class="reciveing-address-icon"></i>
			</van-cell>
			<van-cell title="我的信息" is-link to="/userInfo">
				<i slot="icon" class="user-info-icon"></i>
			</van-cell>
			<van-cell title="订货企业" is-link to="/orderEnt">
				<i slot="icon" class="order-ent-icon"></i>
			</van-cell>
		</div>
	</div>
</template>

<script>
import orderManage from '@/components/OrderManage.vue'
export default {
	name: 'me',
	components: {
		orderManage
	},
	data () {
		return {
			manageModule: {
				toBeConfirmed: {
					label: '待确认',
					iconSrc: require('@/assets/images/icon/icon-beforeConfirm.png'),
					waring: '0',
					billState: '1',
					showFlag: true
				},
				toBeReceived: {
					label: '待收货',
					iconSrc: require('@/assets/images/icon/icon-beforeReceiving.png'),
					waring: '0',
					billState: '2',
					showFlag: true
				},
				toBeFinished: {
					label: '已完成',
					iconSrc: require('@/assets/images/icon/icon-confirm.png'),
					waring: '0',
					billState: '3',
					showFlag: true
				}
			}
		}
	},
	created () {
		this.getToDOWork()
	},
	methods: {
		seeAllOrderHandle () {
			this.$router.push('/orderList')
		},
		billClickHandler (item) {
			const billState = item.billState
			this.$router.push({
				path: '/orderList',
				query: {
					billState
				}
			})
		},
		getToDOWork () {
			this.$fxHttp.requestPost({
				url: `${this.$fxBaseApi.url}/getToDOWork`
			}).then(res => {
				this.setToWork(res)
			})
		},
		setToWork (toWorkList) {
			for (let key in toWorkList) {
				this.manageModule[key].waring = toWorkList[key]
			}
		}
	}
}
</script>
